.App {
  text-align: center;
}

.dh-background {
  background-color: lightblue;
  width: 800px;
  height: 500px;
  border: 3px solid black;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 50px;
}

.dh-gun {
  background-color: black;
  width: 80px;
  height: 10px;
  margin: auto;
  margin-top: 500px;
}

.dh-bullet {
  transition: all 0.5s ease;
  background-color: red;
  width: 5px;
  height: 5px;
  padding: 5px;
  margin-left: -20px;
  margin-top: 75px;
  position: absolute;
}

.dh-duck{
  transition: all 3s ease;
  background-color: blueviolet;
  width: 13px;
  height: 13px;
  padding: 5px;
  margin-left: -20px;
  margin-top: 75px;
  position: absolute;
}